/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
<template>
  <div style="line-height: 1rem; display: inline-block; margin-bottom: 1rem">
    <div class="item-line">
      <v-icon style="color: #fff;padding: 6px;">mdi-phone</v-icon>
      {{ telefone }}
    </div>
    <div class="item-line">
      Chame no WhatsApp
      <v-img class="icon-rotate icon-whatsapp" :src="logo_whatsapp" @click="openWhatsapp()" />
    </div>
    <div class="item-line">
      Chame no Telegram
      <v-img class="icon-rotate icon-telegram" :src="logo_telegram" @click="openTelegram()" />
    </div>
  </div>
</template>

<script>
import logo_telegram from "@/assets/logo_telegram.svg";
import logo_whatsapp from "@/assets/logo_whatsapp.svg";

export default {
  props: {
    telefone: {
      type: String,
      required: true,
    },
  },
  data: () => ({
    logo_telegram,
    logo_whatsapp
  }),

  methods: {
    openWhatsapp: function () {
      let fone = '55'+ this.telefone.replace(/\s/g, '');
      let url="https://api.whatsapp.com/send?phone=" + fone;
      window.open(url);
    },
    openTelegram: function () {
      let url = "https://t.me/RodrigoMichel";
      window.open(url);
    },
  },
};
</script>

